.footer {
  background-color: rgb(227, 238, 241);
  
  @media (min-width: 640px) {
    background-color: #fff;
  }
  
  &__container {
    background-color: #2c2c2c;
    padding: 72px 0 80px;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    
    @media (min-width: 640px) {
      padding-top: 180px;
    }
  }
  
  &__title {
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    font-size: 32px;
    line-height: 42px;
    color: #fff;
    margin-bottom: 48px;
  }
  
  &__button {
    background-color: #144ed5;
    margin-bottom: 80px;

    @media (min-width: 640px) {
      margin-bottom: 120px;
    }
  }
  
  &__name {
    font-family: "Poppins", sans-serif;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #fff;
    opacity: 0.5;
    margin-bottom: 8px;
  
    &-last {
      margin-bottom: 16px;
    }
  }
  
  &__text {
    margin-bottom: 40px;
  }
  
  &__link {
    display: block;
    text-decoration: none;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    font-size: 24px;
    line-height: 36px;
    color: #fff;
    width: 200px;

    @media (min-width: 640px) {
      width: 360px;
    }

    &:hover {
      opacity: 0.6;
    }
  }
  
  &__pictures {
    display: flex;
    gap: 32px;
    align-items: center;
  }
  
  &__icon {
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-position: center;

  
    &--facebook {
      background-image: url(/images/icon/facebook.png);

      &:hover {
        background-image: url(/images//icon/contact--fb--active.svg);
      }
    }

    &--twitter {
      background-image: url(/images/icon/twitter.png);

      &:hover {
        background-image: url(/images//icon/contact--tw--active.svg);
      }
    }

    &--instagram {
      background-image: url(/images/icon/instagram.png);

      &:hover {
        background-image: url(/images//icon/contact--inst--active.svg);
      }
    }

    
  }
}
